#stars-container {
    position: absolute;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    z-index: -1;
}

.star {
    position: absolute;
    bottom: 0;
    width: 1px;
    height: 1px;
    background-color: #fff;
    opacity: 1;
    animation-name: moveStar;
    animation-iteration-count: 1;
    animation-timing-function: linear;
}

.medium-star {
    width: 2px;
    height: 2px;
    animation-duration: 3s;
}

.large-star {
    width: 5px;
    height: 5px;
    animation-duration: 4s;
}

.garnet-star {
    background-color: #800020; /* Цвет граната */
}

@keyframes moveStar {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-100vh);
    }
}
