.customButton {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    width: 350px;
    height: 70px;
    border-radius: 105px;
    border: 1px solid #fff;
    background-color: transparent !important;
    overflow: hidden;
    color: #8743EDFF;
    font-size: 18px;
    cursor: pointer;
    transition: color 1.2s ease, border-color 1.2s ease;
    z-index: 2;
    clip-path: circle(100% at 50% 100%);
}

.customButton:hover {
    color: #fff;
    border-color: #8743EDFF;
}

.customButton::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 100%;
    background: radial-gradient(circle at center bottom, #8743EDFF 1%, transparent 1%);
    transition: width 1.2s ease;
    z-index: -1;
}

.customButton:hover::before {
    width: 11000%;
}

.customButton:active {
    transition: color 0.4s ease, border-color 0.4s ease, clip-path 0.4s ease;
    clip-path: circle(0% at 50% 100%);
}

.customButton.clicked {
    transition: color 0.4s ease, border-color 0.4s ease, clip-path 0.4s ease;
    clip-path: circle(0% at 50% 100%);
}
